import MainLayout from "@/components/layout/MainLayout.vue";
import RegionJson from "@/assets/json/region.json";
import axios from "axios";
import { mapGetters } from "vuex";
import store from "@/store";

const storageURL = process.env.VUE_STORAGE_PATH;

// const storageURL = "http://localhost/school_api/storage";
// const storageURL = "https://stg-kyaung.bib-apps.com/backend/storage";
// const storageURL = "https://kyaung.bib-apps.com/backend/storage";

export default {
    name: "detailStudent",

    components: {
        MainLayout
    },

    data() {
        return {
            student: {},
            image_profile: "",
            regionArr: RegionJson
        };
    },

    computed: {
        ...mapGetters(["teacherId", "studentId", "userType"]),
    },

    methods: {
        setAltImg(event) {
            event.target.src = "data:image/png;base64,"
        },

        getNrcBack(student_id) {
            return storageURL + "/student" + "/" + student_id + "/nrcback.png";
        },

        getNrcFront(student_id) {
            return storageURL + "/student" + "/" + student_id + "/nrcfront.png";
        },

        getProfile(student_id) {
            return storageURL + "/student" + "/" + student_id + "/profile.png";
        },
        
        getDetailStudent() {
            axios.get("/student/detail/" + this.studentId)
                .then((response) => {
                    this.student = response.data;
                })
                .catch((error) => {
                    console.log("Student Detail API Error", error);
                });
        },

        notificationCount() {
            var id = null;
            var type = this.userType;
            if (this.teacherId) {
                id = this.teacherId;
            }
            if (this.studentId) {
                id = this.studentId;
            }
            axios.get("teacher/notificationCount", {
                params: {
                    id: id,
                    type: type
                }
            })
                .then(response => {
                    store.commit("setNotificationCount", response.data);
                })
                .catch(error => {
                    console.log(error, "error");
                });
        },
    },

    created() {
        this.getDetailStudent();
        this.notificationCount();
    }
};