import MainLayout from "@/components/layout/MainLayout.vue";
import RegionJson from "@/assets/json/region.json";
import axios from "axios";
import { mapGetters } from "vuex";
import store from "@/store";

export default {
    name: "lookTeacherProfile",

    components: {
        MainLayout
    },

    data() {
        return {
            teacher: {},
            regionArr: RegionJson
        };
    },

    computed: {
        ...mapGetters(["teacherId", "studentId", "userType"]),
    },

    methods: {
        setAltImg(event) {
            event.target.src = "data:image/png;base64,"
        },

        getDetailTeacher() {
            axios.get("/teacher/profile/" + this.$route.params.id)
                .then(response => {
                    this.teacher = response.data;
                })
                .catch(error => {
                    console.log(error, "error");
                });
        },

        notificationCount() {
            var id = null;
            var type = this.userType;
            if (this.teacherId) {
                id = this.teacherId;
            }
            if (this.studentId) {
                id = this.studentId;
            }
            axios.get("teacher/notificationCount", {
                params: {
                    id: id,
                    type: type
                }
            })
                .then(response => {
                    store.commit("setNotificationCount", response.data);
                })
                .catch(error => {
                    console.log(error, "error");
                });
        },
    },

    created() {
        this.getDetailTeacher();
        this.notificationCount();
    }
}